<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      title: "Your Title",
      fontSizeTitle: 36,
      fontWeightTitle: 700,
      letterSpacingTitle: 1,
      colorTitle: "#303030",
      textAlignTitle: "center",

      intro: "An optional intro paragraph goes here...",
      fontSizeIntro: 18,
      fontWeightIntro: 500,
      letterSpacingIntro: 1,
      colorIntro: "#303030",
      textAlignIntro: "left",

      imageGridCols: 3,

      imageOptions: [],
      paragraphOptions: [],
      buttonsAlignment: "center",
      buttonOptions: [],

      sampleCode: "",
    };
  },
  methods: {
    getSampleCode() {
      // A intro paragraph is optional.
      let intro = "";
      if (this.intro.length > 0) {
        intro = `
<!-- INTRO PARAGRAPH -->
<p
  style="color: ${this.colorIntro};
  letter-spacing: ${this.letterSpacingIntro}px;
  font-size: ${this.fontSizeIntro}px;
  font-weight: ${this.fontWeightIntro};
  margin: 0 auto;
  margin-bottom: 1.5em;
  text-align: ${this.textAlignIntro};
  width: 90%;"
>
  ${this.intro}
</p>
<!-- END INTRO PARAGRAPH -->`;
      }

      // Image cards
      let imageCards = "";
      if (this.imageOptions.length > 0) {
        let i = 0;
        for (let image of this.imageOptions) {
          i++;
          let figcaption = "";
          if (image.imageCaption.length > 0) {
            figcaption = `

    <figcaption
      style="color: ${image.imageCaptionColor};
      letter-spacing: ${image.imageCaptionSpacing}px;
      font-size: ${image.imageCaptionFontSize}px';
      font-weight: ${image.imageCaptionWeight};
      margin: 1rem auto;
      margin-bottom: 1.5em;
      text-align: ${image.imageCaptionTextAlign};"
    >
      ${image.imageCaption}
    </figcaption>
`;
          }

          let imageCard = `
  <!-- IMAGE #${i} -->
  <figure style="height: auto; margin: 0; width: 100%">
    <img
      alt="${image.imageAlt}"
      src="${image.imageUrl}"
      style="display: block; height: auto; width: 100%"
    />${figcaption}
  </figure>
  <!-- END IMAGE #${i} -->
`;

          imageCards = imageCards + imageCard;
        }
      }

      // Grid wrapper
      let gridWrapper = "";
      if (this.imageOptions.length > 0) {
        gridWrapper = `<!-- IMAGE GRID -->
<div
  style="display: grid;
  gap: 1rem;
  grid-template-columns: repeat(${this.imageGridCols}, 1fr);
  margin: 0 auto 48px;
  width: 90%;"
>
    ${imageCards}
</div>
<!-- END IMAGE GRID -->`;
      }

      // Body Paragraphs
      let paragraphs = "";
      if (this.paragraphOptions.length > 0) {
        let i = 0;
        for (let copy of this.paragraphOptions) {
          i++;
          let paragraph = "";
          if (copy.paragraphText) {
            // Copy index value
            let indexValue = i;

            paragraph = `
<!-- PARAGRAPH #${indexValue} -->
<p
  style="color: ${copy.paragraphTextColor};
  letter-spacing: ${copy.paragraphTextSpacing}px;
  font-size: ${copy.paragraphTextFontSize}px;
  font-weight: ${copy.paragraphTextWeight};
  margin: .5rem auto;
  margin-bottom: 1.5em;
  text-align: ${copy.paragraphTextAlign};"
>
  ${copy.paragraphText}
</p>
<!-- END PARAGRAPH #${indexValue} -->
`;
          }

          paragraphs = paragraphs + paragraph;
        }
      }

      // Buttons
      let buttons = "";
      if (this.buttonOptions.length > 0) {
        let i = 0;
        for (let copy of this.buttonOptions) {
          i++;
          let button = "";
          if (copy.buttonText) {
            // Copy index value
            let indexValue = i;

            button = `
  <!-- BUTTON #${indexValue} -->
  <a
    href="${copy.buttonUrl}"
    style="background: ${copy.buttonBgColor};
    border-radius: ${copy.buttonBorderRadius}px;
    color: ${copy.buttonTextColor};
    display: inline-block;
    flex-shrink: 0;
    letter-spacing: ${copy.buttonTextSpacing}px;
    font-size: ${copy.buttonTextFontSize}px;
    font-weight: ${copy.buttonTextWeight};
    margin-bottom: 1.5em;
    padding: ${copy.buttonPadding}px;
    text-decoration: none;"
    target="_blank"
  >
    ${copy.buttonText}
  </a>
  <!-- END BUTTON #${indexValue} -->
`;
          }

          buttons = buttons + button;
        }
      }

      // Button wrapper
      let buttonWrapper = "";
      if (this.buttonOptions.length > 0) {
        buttonWrapper = `<!-- BUTTON WRAPPER -->
<div
  style="display: flex;
  justify-content: ${this.buttonsAlignment};
  gap: '1rem',
  margin: 0 auto 48px;
  width: 90%;"
>
    ${buttons}
</div>
<!-- END BUTTON WRAPPER -->`;
      }

      const heading = `<!-- HEADING -->
<h1
  style="color: ${this.colorTitle};
  letter-spacing: ${this.letterSpacingTitle}px;
  font-size: ${this.fontSizeTitle}px;
  font-weight: ${this.fontWeightTitle};
  margin: 0 auto;
  padding: 0 5% 24px;
  text-align: ${this.textAlignTitle};
  width: 90%;"
>
  ${this.title}
</h1>
<!-- END HEADING -->
${intro}

${gridWrapper}
${paragraphs}
${buttonWrapper}`;

      return heading;
    },
  },
};
</script>

